
import axios from 'axios';
import XHeader from 'vux/src/components/x-header/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Picker from 'vux/src/components/picker/index.vue'


export default {
components: {
XHeader,
GroupTitle,
Flexbox,
FlexboxItem,
XButton,
DatetimePlugin,
Datetime,
Group,
Picker
},
data() {
return {
value1: '',
startTime: '',
endTime: '',
activeType: '',
activityName: '',
activePace: '',
activeTitle: '',
activeCreatePeople: '',
activePrincipalPeople: '',
activeContex: '',
activeContent: '',
activeStatus: 0,
activeProjectActive: '',
hot: '',
hourListValue: '',
PickerVisible1: false,
pickerValue1: '请选择活动类型',
years: '',
year1: [''],
list: '',
PickerVisible2: false,
departmentid: this.$store.getters.user.departmentid
};
},
methods: {
openPicker() {
this.$refs.picker.open();
},
handleConfirm(value) {
var d = value;
this.startTime =
d.getFullYear() +
'-' +
(d.getMonth() + 1) +
'-' +
d.getDate() +
' ' +
d.getHours() +
':' +
d.getMinutes() +
'';
},
handlePicker() {
this.PickerVisible2 = true;
axios({
method: 'get',
url: '/dangjian/pscoredetail/queryByJoinList'
})
.then(res => {
this.list = res.data;
})
.catch(function(error) {
console.log(error);
});
},
onValuesChange(picker, values) {
this.pickerValue1 = values[0];
this.PickerVisible1 = false;
},
submit() {
alert(this.activeType);
axios({
method: 'post',
url: '/dangjian/active/create',
params: {
startTime: new Date(this.startTime.replace(/-/g, '/')).getTime(),
endTime: new Date(this.endTime.replace(/-/g, '/')).getTime(),
activeType: this.activeType,
activityProjectId: this.activeProjectActive,
activePace: this.activePace,
activeCreatePeople: this.activeCreatePeople,
activePrincipalPeople: this.activePrincipalPeople,
activeContex: this.activeContex,
activeStatus: this.activeStatus,
departmentid: this.departmentid
}
})
.then(res => {
console.log(res);
})
.catch(function(error) {
console.log(error);
});
},
submit1(it) {
this.activeType = it.id;
this.activeProjectActive = it.projectId;
this.activityName = it.projectName;
this.PickerVisible2 = false;
},
getActivity() {},
log(str1, str2 = '') {
console.log(str1, str2);
},
showPlugin() {
this.$vux.datetime.show({
cancelText: '取消',
confirmText: '确定',
format: 'YYYY-MM-DD HH',
value: '2017-05-20 18',
onConfirm(val) {
console.log('plugin confirm', val);
},
onShow() {
console.log('plugin show');
},
onHide() {
console.log('plugin hide');
}
});
},
toggleFormat() {
this.format = this.format === 'YYYY-MM-DD HH:mm' ? 'YYYY-MM-DD' : 'YYYY-MM-DD HH:mm';
},
change(value) {
this.startTime = value;
},
change1(value) {
this.endTime = value;
},
clearValue(value) {
this.value6 = '';
},
clearValue8(value) {
this.value8 = '';
},
setToday(value) {
let now = new Date();
let cmonth = now.getMonth() + 1;
let day = now.getDate();
if (cmonth < 10) cmonth = '0' + cmonth;
if (day < 10) day = '0' + day;
this.value7 = now.getFullYear() + '-' + cmonth + '-' + day;
console.log('set today ok');
}
},
mounted() {
this.getActivity();
}
};



